import React, { Component } from 'react';
import CheckoutSummary from '../../Componenets/UI/Order/CheckoutSummary/CheckoutSummary';
import { Route } from 'react-router-dom';
import Contactdata from '../CheckOut/Contactdata/Contactdata';

class Checkout extends Component{
              state={
                  ingredients:null,
                  totaprice:0
              }

              componentWillMount(){
                  const query=new URLSearchParams(this.props.location.search);
                  let ingredients={}

                  let price=0;
                  for(let param of query.entries())
                  {
                      if(param[0]==='price')
                      {
                          price=param[1];

                      }
                      else
                      {
                     ingredients[param[0]]=+param[1];
                    }
                  }
                  this.setState({ingredients:ingredients,totaprice:price });
              }

       checkoutCancelHandler=()=>{
         this.props.history.goBack();
       }
       checkoutContinueHandler=()=>{
          this.props.history.push('/checkout/contactdata');
    }



      

    render(){
        return(
            <div>
                <CheckoutSummary ingredients={this.state.ingredients} 
                checkoutCancel={this.checkoutCancelHandler}
                checkoutContinue={this.checkoutContinueHandler}
                />
                <Route path={this.props.match.path +'/contactdata'} render={(props)=>(<Contactdata  ingredients={this.state.ingredients} price={this.state.totaprice} {...props}/>)}/> 
            </div>
        )
    }

}
export default Checkout;