import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import Burger from '../../Componenets/Burger/Burger';
import BuildControls from '../../Componenets/Burger/BuildControls/BuildControls';
import OrderSummary from '../../Componenets/Burger/OrderSummary/OrderSummary';
import Modal from '../../Componenets/UI/Modal/Modal';

import axios from '../../Axios';
import Spinner from '../../Componenets/UI/Spinner/Spinner';

import withErrorHandler from '../../WithErrorHandler/WithErrorHandler'


const INGREDIENT_PRICE = {
    salad: 5,
    cheese: 10,
    meat: 20,
    bacon: 12
}
class BurgerBuilder extends Component {
    state = {
        ingredients: null,
        totalPrice: 10,
        purchasable: false,
        purchasing: false,
        loading: false,
        error :false

    }

    componentDidMount() {

        console.log(this.props);
        axios.get('https://react-burger-builder-207a2.firebaseio.com/ingredients.jso').then(response=>{
            this.setState({ingredients:response.data})
            console.log(response.data);

        }).catch(error=>{
            this.setState({error:true})
            //console.log(error);
        })

    }

    updatePurchaseable = (ingredients) => {
        let updtaedingredients = Object.keys(ingredients).map(igkey =>
            ingredients[igkey]
        );

        let sum = 0;
        for (let k in updtaedingredients) {
            sum = sum + updtaedingredients[k];
        }


        if (sum > 0) {
            this.setState({ purchasable: true })
        }
        else {
            this.setState({ purchasable: false })
        }

        //console.log(updtaedingredients);
    }



    addIngredientHandler = (type) => {
        const oldCount = this.state.ingredients[type];
        const updateCount = oldCount + 1;
        const updatedIngredient = {
            ...this.state.ingredients
        };
        updatedIngredient[type] = updateCount;
        const pricseAddition = INGREDIENT_PRICE[type];
        const oldPrice = this.state.totalPrice;
        const newPrice = oldPrice + pricseAddition;
        //console.log(newPrice); 
        this.setState({
            totalPrice: newPrice,
            ingredients: updatedIngredient,
        });
        this.updatePurchaseable(updatedIngredient);
    }
    removeIngredients = (type) => {
        const oldCount = this.state.ingredients[type];
        let updateCount;
        if (oldCount === 0) {
            updateCount = 0;

        }
        else
            updateCount = oldCount - 1;

        const updatedIngredient = {
            ...this.state.ingredients
        };
        updatedIngredient[type] = updateCount;
        const pricseAddition = INGREDIENT_PRICE[type];
        const oldPrice = this.state.totalPrice;
        const newPrice = oldPrice - pricseAddition;
        //console.log(newPrice); 
        this.setState({
            totalPrice: newPrice,
            ingredients: updatedIngredient,
        });
        this.updatePurchaseable(updatedIngredient);
    }
    purchaseHandler = () => {
        console.log(this.state.purchasing);
        this.setState({ purchasing: true })


    }
    modalClosedHandler = () => {
        this.setState({ purchasing: false })
    }
    orderContinue = () => {

       
         const querParam=[];
         for(let i in this.state.ingredients){
             querParam.push(encodeURIComponent(i)+ '=' +encodeURIComponent(this.state.ingredients[i]));
         }
         querParam.push('price='+this.state.totalPrice);
         const querystring=querParam.join('&');
          this.props.history.push({
              pathname:'/checkout',
              search:'?'+querystring
          })
         
        


    }


    render() {
        const disabledInfo = {
            ...this.state.ingredients
        }
        for (let key in disabledInfo) {
            disabledInfo[key] = disabledInfo[key] <= 0;
        }
        let ordersummary =null;
        
       

        let burger= this.state.error?<p style={{textAlign:'center'}} >Ingrdients can't be loaded </p> : <Spinner/>
        if(this.state.ingredients)
        {
            burger=(
              <Aux>
            <Burger ingredients={this.state.ingredients} />
            <BuildControls ingredientAdded={this.addIngredientHandler}
                ingredientRemove={this.removeIngredients}
                disabled={disabledInfo}
                price={this.state.totalPrice}
                purchasable={this.state.purchasable}
                orderedNow={this.purchaseHandler}
            />
            </Aux>
            );
            ordersummary= <OrderSummary ingredients={this.state.ingredients} 
            cancelOrder={this.modalClosedHandler} 
            continueOrder={this.orderContinue}
            price={this.state.totalPrice}
            />;

        }
    

        if (this.state.loading) {
            ordersummary = <Spinner />
        }


        return (
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.modalClosedHandler}>
                    {ordersummary}
                </Modal>
                {burger}
            </Aux>
        )
    }
}

export default withErrorHandler(BurgerBuilder, axios);