import React, { Component } from 'react';
import Button from '../../../Componenets/UI/Button/Button';
import classes from './Contactdata.css';
import axios from '../../../Axios'
import Spinner from '../../../Componenets/UI/Spinner/Spinner';
class Contactdata extends Component{
         state={
             name:'',
             email:'',
             address:{
                 village:'',
                 post:''
             },
            loading:false

         }
     orderHandler=(event)=>{
             event.preventDefault();
        this.setState({ loading: true });
        const data = {
            ingredients: this.props.ingredients,
            price: this.props.price,
            name: 'Bimal',
            address: {
                village: 'uttarbarh',
                pin: 721148,
                district: 'Paschim Medinipur',
                State: 'West Bengal'
            },
            email: 'bimal1997@gmail.com',
            delhivery: 'Fastest'

        }


        //alert("You are continued");
        axios.post('/orders.json', data).then(response => {

            this.setState({ loading: false })
            this.props.history.push('/');  
           // this.modalClosedHandler();
         console.log(response)
        }).catch(error => {
            this.setState({ loading: false })
            console.log(error)
        });
              



             console.log(this.props.ingredients);

         }


   render(){
        let form=(<form>
            <input   className={classes.Input} type="text"  name="name" placeholder="Enter Your Name" />
            <input className={classes.Input} type="email"  name="email" placeholder="Enter Your Mail" />
            <input className={classes.Input} type="text"  name="village" placeholder="Enter Your Village" />
            <input  className={classes.Input} type="number"  name="post" placeholder="Enter Your postalcode" />
            <Button type="Success" clicked={this.orderHandler}>Order</Button>
        </form>)
        if(this.state.loading)
        {
            form=<Spinner/>
        }

       return(
            <div className={classes.Contactdata}>
                <h1>Enter Your Deatils Here</h1>
                {form}
            </div>
       )
   }



}
export default Contactdata;